.arrowDown{
    position: relative;
    padding-right: 18px;
    &::after{
        position: absolute;
        content: "\E008";
        font-family: "icons";
        color: currentColor;
        font-size: 5px;
        right: 0;
        top: calc(50% - 2.5px);
        transition: all .3s ease-out;
    }
    &:hover{
        &::after{
            transform: rotate(180deg);
            transition: all .3s ease-out;
        }
        &> .dropdown{
            display: block;
        }
    }
}